// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import 'weui/dist/style/weui.min.css'
import 'swiper/dist/css/swiper.css'
// import '../assets/icon/inonfont.css'
// import '@assets/iconfont.css'

import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false


import axios from 'axios'
import VueAxios from 'vue-axios'

// import VueLazyload from 'vue-lazyload'

// import quickMenu from 'vue-quick-menu'
// Vue.component(quickMenu.name,quickMenu)

import VueTimeago from 'vue-timeago'


// var VueAwesomeSwiper = require('vue-awesome-swiper')

import VmBackTop from 'vue-multiple-back-top'


/**
 */
 //  ()
 const wx = require('weixin-js-sdk')
 
 import api from './api';
 

// import VueScrollPicker from 'vue-scroll-picker'

// Vue.use(VueScrollPicker)

Vue.use(VueTimeago, {
    name: 'timeago', // component name, `timeago` by default
    locale: 'zh-CN',
    locales: {
        // you will need json-loader in webpack 1
        // 'zh-CN': require('vue-timeago/locales/zh-CN.json')
        // 
        'zh-CN':[
            "",
            "",
            "",
            "%sh",
            "%sd",
            "%sw",
            "%sm",
            "%sy"
            ]
    }
})

Vue.use(VueAxios, axios)
// Vue.use(VueLazyload)




Vue.component(VmBackTop.name, VmBackTop)

// mount with global
// Vue.use(VueAwesomeSwiper)

// router.afterEach(function () {
//     alert("after");
//     });

    router.beforeEach((to, from, next) => {

        // var __to = localStorage.getItem("__to")||''
        // // to  from  
        
        // // console.log(to.path ,from.path , __to ,to.path )
        // if(to.path != from.path && __to !=to.path ){
        //   localStorage.removeItem("articles")
        //   localStorage.removeItem("rank")
        // //   console.log("clear cache")
        // }
        
        // localStorage.setItem("__to",to.path)
        // console.log(to.path, from.path)
        next()
      })

      router.afterEach((to, from) => {
        // ...
        setTimeout(function(){
            api.get("/jssdk?url=http://readfollow.com"+to.path,function(err,data){
                wx.config({
                  debug: false, // ,apialertpclogpc
                  appId: "wx267866e82ab809fc", // 
                  timestamp: data.timestamp, // 
                  nonceStr: data.nonceStr, // 
                  signature: data.signature, // 1
                  jsApiList: ['checkJsApi', 'onMenuShareTimeline', 'onMenuShareAppMessage'] // JSJS2
                })
            
                
                wx.ready(function () {
                    wx.checkJsApi({jsApiList: ['checkJsApi', 'onMenuShareTimeline', 'onMenuShareAppMessage']});
                    wx.onMenuShareTimeline({
                        title: '',
                        link: "http://readfollow.com"+to.path,
                        imgUrl: 'http://readfollow.com/logo.png'
                    });
                    wx.onMenuShareAppMessage({
                        title: '',
                        desc: '',
                        link: "http://readfollow.com"+to.path,
                        imgUrl: 'http://readfollow.com/logo.png'
                    })
                })
              })
        }, 200);
      })
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



