<template>
<div>

<div  v-if="showSuccess">
    <div class="weui-mask"></div>
    <div class="weui-dialog">
        <div class="weui-dialog__hd"><strong class="weui-dialog__title"></strong></div>
        <div class="weui-dialog__bd"></div>
        <div class="weui-dialog__ft">
            <a href="javascript:;" v-on:click="showSuccess = false" class="weui-dialog__btn weui-dialog__btn_primary"></a>
        </div>
    </div>
</div>
<div  v-if="showWarn">
    <div class="weui-mask"></div>
    <div class="weui-dialog">
        <div class="weui-dialog__hd"><strong class="weui-dialog__title"></strong></div>
        <div class="weui-dialog__bd"></div>
        <div class="weui-dialog__ft">
            <a href="javascript:;" v-on:click="showSuccess = false" class="weui-dialog__btn weui-dialog__btn_primary"></a>
        </div>
    </div>
</div>

<form @submit.prevent="submit">
    <div class="weui-cells__title"> > </div>
    <div class="weui-cells__title"></div>
    <weui-textarea placeholder="" v-model="url"></weui-textarea>
    <div class="weui-btn-area">
      <button type="submit" class="weui-btn weui-btn_primary"></button>
    </div>
</form>
</div>
</template>

<script>
import api from '../../api';
import WeuiTextarea from '@/components/weui/Textarea';

  export default {
    name: 'app',
    data(){
        return{
           url:"",
           showSuccess:false,
           showWarn:false,
        }
    },
    methods: {
       submit: function(event) {
          var site = this
          if(site.url != ''){
            setTimeout(function(){
              site.posting = false
            }, 200);

            site.posting = true
              api.post(site.url,function(err,data){
                if(data == "0"){
                  alert('')
                }else{
                    if(site.url == ''){
                      site.showSuccess = true
                    }
                    site.url = ''
                }
              });
          }else{
            site.showWarn = true
          }
        }
    },
    components: {
      'weui-textarea':WeuiTextarea
    }
  }
</script>



